.transition-iban {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #0053FA;
  height: 100%;
  min-height: 100vh;
}
.transition-iban svg {
  max-width: 600px;
  max-height: 500px;
  width: 100%;
}

.iban-input .copy-btn {
  background: black;
  font-weight: 700;
  font-size: 11px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  padding: 5px 15px;
  text-transform: uppercase;
  cursor: pointer;
}
.iban-input .copy-btn svg {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-20%, -50%);
          transform: translate(-20%, -50%);
  width: 16px;
}
.iban-input .copy-btn.copied {
  color: black;
}
.iban-input .copy-btn.copied svg {
  opacity: 1;
}
.iban-wrapper .ant-form-item-label:before,
.iban-wrapper input {
  background: #0053FA !important;
  color: white !important;
}
.iban-wrapper .ant-form-item-label label {
  color: white !important;
}

